/* Table Base */
.table {
  font-family: arial;
  max-width: 100%;
  background-color: transparent;
  border-collapse: collapse;
  border-spacing: 0;
  width: 100%;
  margin-bottom: 20px; }
  .table :global tbody tr td {
    font-weight: normal;
    font-size: 12px;
    padding: 8px 15px;
    line-height: 20px;
    text-align: left;
    vertical-align: middle;
    border-top: 1px solid #dddddd; }
    .table :global tbody tr td.t-active {
      color: #46a546; }
    .table :global tbody tr td.t-inactive {
      color: #e00300; }
    .table :global tbody tr td.t-draft {
      color: #f89406; }
    .table :global tbody tr td.t-scheduled {
      color: #049cdb; }
  .table :global thead th {
    background: #eeeeee;
    vertical-align: bottom;
    font-weight: bold;
    font-size: 12px;
    padding: 8px 15px;
    line-height: 20px;
    text-align: left;
    vertical-align: middle;
    border-top: 1px solid #dddddd; }
    .table :global thead th.t-small {
      width: 5%; }
    .table :global thead th.t-medium {
      width: 15%; }
  .table :global tbody > tr:nth-child(odd) > td,
  .table :global tbody > tr:nth-child(odd) > th {
    background-color: #fafafa; }

/* Small Sizes */
@media (max-width: 480px) {
  .table-action thead {
    display: none; }
  .table-action tr {
    border-bottom: 1px solid #dddddd; }
  .table-action td {
    border: 0; }
  .table-action td:not(:first-child) {
    display: block; } }
